import React, { useState, useEffect } from "react";
import { BASE_URL, FILES_URL } from "../../constants/index";
import axios from "axios";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";
const About = () => {
  const [poinOfSellData, setpoinOfSellData] = useState([]);
  const [sell_point_id, setsell_point_id] = useState(
    "608b599dce9aa300229f9b87"
  );
  const [pImage, setpImage] = useState();
  const [address, setAddress] = React.useState("");
  const [coordinates, setCoordinates] = React.useState({
    lat: null,
    lng: null,
  });

  // const profileImage = `${FILES_URL}${poinOfSellData.cpImage}`
  const handleSelect = async (value) => {
    const results = await geocodeByAddress(value);
    const latLng = await getLatLng(results[0]);
    setAddress(value);
    setCoordinates(latLng);
  };

  const fetch = async () => {
    const body = {
      spId: sell_point_id,
    };
    axios
      .post(`${BASE_URL}/getSPIfo`, body)
      .then((response) => {
        // console.log(response.data);
        setpoinOfSellData(response.data);
      })
      .catch((err) => console.log(err));
  };

  const UpdateProfileHandler = () => {
    console.log(address);
  };

  const onFileChange = (e) => {
    const pImage = e.target.files[0];
    setpImage(pImage);
  };

  var profileImage = FILES_URL + poinOfSellData.cpImage;

  useEffect(() => {
    fetch();
  }, [1000]);

  return (
    <Container>
      <Row>
        <Col>
          <img
            style={{ width: 200, height: 200, borderRadius: 10, marginTop: 10 }}
            src={profileImage}
            alt="profils pic"
          />
        </Col>
        <Col>
          <h1>User Profile</h1>
          <Form className="form">
            {/* <p>{this.state.msg}</p> */}
            <Form.Group controlId="formCategory1">
              <Form.Label>إسم المحل </Form.Label>
              <Form.Control type="text" defaultValue={poinOfSellData.cpName} />
            </Form.Group>
            <Form.Group controlId="formCategory2">
              <Form.Label>البريد الالكتروني </Form.Label>
              <Form.Control type="email" defaultValue={poinOfSellData.email} />
            </Form.Group>

            <Form.Group controlId="formCategory4">
              <Form.Label>صورة نقطة البيع </Form.Label>
              <Form.Control
                type="file"
                name="profileImage"
                onChange={(e) => onFileChange(e)}
              />
            </Form.Group>
            <Form.Group controlId="formCategory1">
              <Form.Label> موقع نقطة البيع  </Form.Label>
            <PlacesAutocomplete
              value={address}
              onChange={setAddress}
              onSelect={handleSelect}
            >
              {({
                getInputProps,
                suggestions,
                getSuggestionItemProps,
                loading,
              }) => (
                <div>
                  <p>Latitude: {coordinates.lat}</p>
                  <p>Longitude: {coordinates.lng}</p>

                  <input {...getInputProps({ placeholder: "إبحث عن الموقع" })} />

                  <div>
                    {loading ? <div>...loading</div> : null}

                    {suggestions.map((suggestion) => {
                      const style = {
                        backgroundColor: suggestion.active ? "#41b6e6" : "#fff",
                      };

                      return (
                        <div {...getSuggestionItemProps(suggestion, { style })}>
                          {suggestion.description}
                        </div>
                      );
                    })}
                  </div>
                </div>
              )}
            </PlacesAutocomplete>
            </Form.Group>
            <Button variant="primary" onClick={UpdateProfileHandler}>
              تحديث البيانات
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
};

export default About;
